
.edCard{
    background-color: rgb(37, 49, 49);
    border-color: rgb(7, 243, 66);
    color:rgb(7, 243, 66);
}
.schoolimg{
    border-color: rgb(7, 243, 66);
}

.binary{
    
    height:140vh;
    width: 100vw;
    position: relative;
    margin-left: 220px;
    margin-top: -371px;
    z-index: 1;
}
.education{
    display:grid;
    grid-template-columns: repeat( auto-fit, minmax(400px, 1fr) );
}
.card{
  position: relative;
  z-index: 2;
}
@media only screen and (max-width: 768px) {
    .binary{
        display: none;
    }
    
}