.skill{
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(400px,1fr));
   
}
.wrapper{
    display:flex;
    
}
.mypc{
    z-index: 1;
}
.content{
    z-index: 3;
    border-width: 10px;
    margin-left: -265px;
}
.table{
    border-radius: 2%;
    border-color: crimson;
    background-color: rgb(64, 72, 95);
    width:33vw;
    height:50vh;
}
@media only screen and (max-width: 768px) {
    .mypc{
        z-index:-1;
    }
    .content{
        justify-content: center;
        
    }
    .table{
        width: 80vw;
    }
}