.tc{
  margin-top: 0px;
}
.potrait{
    height:80vh;
    width: 60vw;
    z-index:0;
    
}

.button{
    z-index:3;
    width:100px;
    height:50px;
    border-color: black;
}
.art{
    width:50vw;
    z-index: 3;
}
.introduction{
    width: 33 vw;
    height: fixed;
    z-index: 1;
}
@media only screen and (min-width: 600px) {
   
   .grid-container{
    display:grid;
    grid-template-columns: 1fr 2fr;
   
    }
}

.potrait {
    animation: fadeIn ease 20s;
}
@keyframes fadeIn {
    0% {
      opacity:0;
    }
    100% {
      opacity:1;
    }
}
.intro1{
    animation: fadeIn ease 3s;
    font-size: 2.25rem;
}
