@media only screen and (max-width: 1000px) {
  .img{
      display:none;
  }
  
  .art-container{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
  }
  .intro1{
    font-size: 1.5rem;
  }
  .intro2{
    font-size: 0.75rem;
  }
  .intro3{
    font-size: 0.5rem;
  }
  .intro4{
