.Tilt{
    grid-column: 2/3;
    grid-row: 2/3;
    /* height: 250px;
    width: 250px;  */
}
.Tilt-inner{
    background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);
}
.TiltParent{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    height:100vh;
    width: 100vw;
}
